.practice2 {
  position: relative;
  width: 100vw;
  height: 100vh;
  min-width: 500px;
  min-height: 240px;
  max-width: 100vw;
  max-height: 100vh;
  background-color: #2c2c2c;
  overflow: scroll;
}

/* 화면1 */
.location1-container {
  position: absolute;
  display: block;
  /* background-color: #f5d7ee; */
  width: 100vw;
  height: 800px;
  top: 10%;
  left: 0;
  overflow: hidden;
}
.location1-container input {
  display: none;
}

/* 중앙 피사체 */
.location1-runner-box {
  position: absolute;
  display: block;
  /* background: url('img/penguin.png') center center / 100% no-repeat; */
  width: 200px;
  height: 200px;
  bottom: 70px;
  left: calc(48vw - 60px);
  z-index: 5;
}

/* 슬라이드 전체 */
.location1-slide {
  position: absolute;
  display: block;
  background-color: #fff2fc;
  width: 700vw;
  height: 800px;
  top: 0;
  left: 0;
}

/* 슬라이드 장식물 */
.location1-slide-ground {
  position: absolute;
  display: block;
  /* background: url('img/iceground1.png'); */
