  background-color: #fff2fc;
  transition: 3s;
}
#slide-btn2:checked ~ .location1-slide {
  transform: translate(-200vw);
  background-color: #ccffe1;
  transition: 3s;
}
#slide-btn3:checked ~ .location1-slide {
  transform: translate(-400vw);
  background-color: #292b31;
  transition: 3s;
}
#slide-btn4:checked ~ .location1-slide {
  transform: translate(-600vw);
  background-color: #fff2d6;
  transition: 3.5s;
}

@keyframes arrowhide {
  0% {
    opacity: 0%;
  }
  80% {
    opacity: 0%;
  }
  100% {
    opacity: 100%;
  }
}
